/* _____ Basic elements _____ */

::-webkit-input-placeholder {color: rgba(#1a1a1a, .5);font-size: 18px;}
::-moz-placeholder {color: rgba(#1a1a1a, .5);font-size: 18px;}
:-moz-placeholder {color: rgba(#1a1a1a, .5);font-size: 18px;}
:-ms-input-placeholder {color: rgba(#1a1a1a, .5);font-size: 18px;}

@include breakpoint("phablet") {
	::-webkit-input-placeholder {font-size: 20px;}
	::-moz-placeholder {font-size: 20px;}
	:-moz-placeholder {font-size: 20px;}
	:-ms-input-placeholder {font-size: 20px;}
}
@include breakpoint("small") {
	::-webkit-input-placeholder {font-size: 20px;}
	::-moz-placeholder {font-size: 20px;}
	:-moz-placeholder {font-size: 20px;}
	:-ms-input-placeholder {font-size: 20px;}
}




*, *:before, *:after {
	box-sizing: border-box;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.center{text-align: center !important;}
.text-l{text-align: left !important;}
.text-r{text-align: right !important;}
.clearfix:before, .clearfix:after {content: "";display: table;}
.clearfix:after {clear: both;}
.flow-hidden {overflow: hidden}
.none {
	display: none;
}

/* base */


.container {
	position: relative;
	@extend %clearfix;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;
	@include breakpoint("small") {
		width: $sm-container;
	}
	@include breakpoint("medium") {
		width: $md-container;
	}
	@include breakpoint("large") {
		width: $lg-container;
	}
}

body {
	background-color: #fff;
	position: relative; 
	color: #000;
	font-family: $font1;
	font-weight: 400;
	-webkit-font-smoothing: antialiased;

	line-height: 1.2;
}

/* layout */




ul {
	margin: 0;
	padding: 0;
	li {
		list-style-type: none;
	}
}

h1,h2,h3,h4,h5,h6 {
	line-height: 1,2;
	font-weight: normal;
	padding: 0;
	margin: 0;
}




